.slider{
    height: calc(100vh - 60px);
    width: 100vw;
    position: relative;
    overflow: hidden;
  
    .container{
      width: 300vw;
      height: 100%;
      display: flex;
      transition: all 1s ease;
      // animation: slider 15s ease infinite; 
     // @keyframes slider {
      //   0%   {transform:translateX(0)}
      //   30%  {transform:translateX(0)}
      //   36%  {transform:translateX(-100vw)}
      //   63%  {transform:translateX(-100vw)}
      //   69%  {transform:translateX(-200vw)}
      //   93%  {transform:translateX(-200vw)}
       
      //   100% {transform:translateX(0)}
      // }

      img{
        width: 100vw;
        height: 100%;
        object-fit: cover;
      }

      
    }
  
    .icons{
      width: fit-content;
      display: flex;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 100px;
      gap: 10px;
      margin: auto;
  
      .icon{
        width: 50px;
        height: 50px;
        border: 1px solid #999;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }
    }
  }