

.products {
  padding: 30px 50px;
  display: flex;

  .left {
    flex: 1;
    position: sticky;
    height: 100%;
    top: 50px;
    

    .filterItem{
      margin-bottom: 30px;

     &.filter1{ display: flex;
      flex-direction: column;}

      h2{
        font-weight: 400;
        margin-bottom: 20px;
      }

      
        label{
          margin-left: 10px;
        }
      
    }
  }
  
  .right {
    flex: 3;
   

    .catImg{
      width: 100%;
      height: 200px;
      object-fit: cover;
      margin-bottom: 50px;
    }


  }
}